body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  min-height: 100vh;
  width: 100vw;
  background-color: #328CC1;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #328CC1;
  min-height: 95vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: left;
          align-items: left;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  font-size: calc(10px + 2vmin);
}

.App-footer {
  background-color: #328CC1;
  color: #1d2731;
  font-size: calc(3px + 1.8vmin);
  display: -webkit-flex;
  display: flex;
  margin-left: 1.5vmin;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navbar {
    height: 10vmin;
    background-color:#328CC1;
    z-index: 2000;
    text-align: left;
}

.navbar-light .navbar-nav .nav-link {
    color: #0b3c5d;
}

.navbar-light .navbar-nav .nav-link:focus, .navbar-light .navbar-nav .nav-link:hover {
    color: #1d2731;
}

.dropdown-menu {
    min-width: 6.3vw;
    background-color: transparent;
    position: relative;
}


.carousel.carousel-slider .control-arrow {
    height: 90vmin;
    width: 10vw;
    margin-top: -10vmin;
    margin-bottom: 10vmin;
}

.carousel .slide img {
    /*height: 60vmin !important;*/
    /*width: 80vmin !important;*/
    margin: 5vmin;
}

.fullImage {
    height: 60vmin;
    width: 80vmin;
    margin: 5vmin;
}

.carousel .control-next.control-arrow::before {
    border-left: 16px solid #fff;
}

.carousel .control-prev.control-arrow::before {
    border-right: 16px solid #fff;
}

.carousel .control-dots {
    background-color: rgba(179, 204, 255, 0.5);
}

.carousel .slide .legend {
    background: #e6f0ff !important;
    color: black !important;
    opacity: 0.5;
}

/* .carousel .thumbs-wrapper {
    min-height: 0vmin;
} */

.project-box {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    background-color: #e6f0ff;
    color: #1d2731;
}

.project-cards {
    min-width: 10vw;
    position: absolute;
    top: 0;
    right: 12vw;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.project-text {
    margin-left: 5vmin;
    margin-top: 2.5vmin;;
    margin-right: 10vw;
    text-align: left;
    font-size: calc(2vmin + 0.3vw);
}

.project-title {
    font-weight: bold;
}

.project-head {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    font-size: calc(2.5vmin + 0.5vw);
    margin-left: 5vmin;
    margin-top: 5vmin;
    margin-bottom: 1vmin;
    margin-right: 55vmin;
    text-align: left;
}

.project-blurb {
    font-weight: normal;
    font-size: calc(2vmin + 0.4vw);

}

.project-learned {
    font-size: calc(1.5vmin + 0.5vw);
    text-align: left;
    margin-left: 5vmin;
}

.project-textbox {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    min-height: 60vmin;
}

.project-description {
    text-align: left;
    font-size: calc(1.5vmin + 0.5vw);
    margin: 5vmin;
}
.carousel .slide .legend {
    bottom: 40px;
    left: 0;
    margin-left: 0;
    width: 80vmin;
    border-radius: 0px;
    background: #000;
    color: #fff;
    padding: 0px;
    font-size: 12px;
    text-align: center;
    opacity: .25;
}

.carousel .thumb {
    width: 20vmin;
    border: 3px solid #e6f0ff;
}

.carousel .thumb.selected {
    border: 3px solid #0b3c5d;
}

.carousel .thumb.selected, .carousel .thumb:hover {
    border: 3px solid #0b3c5d;
}

.carousel .slide {
    text-align: left;
}

.card {
    width: 15vw;
    font-weight: bold;
    color: #1d2731;
    background-color: #fff0;
    border: 0px;
}

.bottom-card {
    position: absolute;
    margin-top: 40vh;
}

.card-header {
    font-size: calc(3vmin);
    padding: 1vh;
    background-color: rgba(179, 204, 255, 0.5);
}

.return-button {
    margin-bottom: 5min;
}

.list-group-item {
    font-size: calc(2vmin);
    padding: 1vh;
    background-color: #e6f0ff;
}

.hidden {
    visibility:hidden;
    width: 0px !important;
    height: 0px !important;
}

.visible {
    visibility:visible;
}
